  background: rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: width var(--animation-duration-normal) var(--animation-easing-ease-out), 
              height var(--animation-duration-normal) var(--animation-easing-ease-out);
}

