/* View Toggle */
.view-toggle {
  display: flex;
  background: #f8f9fa;
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid #e9ecef;
}

.view-btn {
  padding: 8px 12px;
  background: transparent;
  border: none;
  cursor: pointer;
  font-size: 16px;
  color: #6c757d;
  transition: all var(--animation-duration-normal) var(--animation-easing-ease-in-out);
  min-width: 40px;
  position: relative;
  overflow: hidden;
}

.view-btn::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  background: rgba(0, 123, 255, 0.1);
  border-radius: 50%;
  transform: translate(-50%, -50%);
