              height var(--animation-duration-normal) var(--animation-easing-ease-out);
}

.view-btn:hover::before {
  width: 100px;
  height: 100px;
}

.view-btn:hover {
  background: #e9ecef;
  color: #495057;
}

.view-btn.active {
