/* List View */
.user-list-view {
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  overflow: hidden;
  margin-bottom: 24px;
}

.list-header {
  display: grid;
  grid-template-columns: 40px 1fr 1fr 120px 100px;
  gap: 16px;
  padding: 16px 20px;
  background: #f8f9fa;
  border-bottom: 1px solid #e9ecef;
  font-weight: 600;
  font-size: 14px;
  color: #495057;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.list-header-cell {
  display: flex;
  align-items: center;
}

.checkbox-cell {
  justify-content: center;
}

.list-body {
  max-height: 600px;
  overflow-y: auto;
}

.list-row {
  display: grid;
  grid-template-columns: 40px 1fr 1fr 120px 100px;
  gap: 16px;
  padding: 16px 20px;
  border-bottom: 1px solid #f1f3f4;
  transition: all var(--animation-duration-normal) var(--animation-easing-ease-in-out);
  cursor: pointer;
  animation: fadeInLeft var(--animation-duration-normal) var(--animation-easing-ease-out);
  animation-fill-mode: both;
}

.list-row:nth-child(1) { animation-delay: 0.1s; }
.list-row:nth-child(2) { animation-delay: 0.15s; }
.list-row:nth-child(3) { animation-delay: 0.2s; }
.list-row:nth-child(4) { animation-delay: 0.25s; }
.list-row:nth-child(5) { animation-delay: 0.3s; }
.list-row:nth-child(6) { animation-delay: 0.35s; }
.list-row:nth-child(7) { animation-delay: 0.4s; }
.list-row:nth-child(8) { animation-delay: 0.45s; }
.list-row:nth-child(9) { animation-delay: 0.5s; }
.list-row:nth-child(10) { animation-delay: 0.55s; }

.list-row:hover {
  background: #f8f9fa;
}

.list-row.selected {
  background: #e3f2fd;
  border-left: 4px solid #007bff;
}

.list-cell {
  display: flex;
  align-items: center;
  font-size: 14px;
}

.checkbox-cell {
  justify-content: center;
}

.checkbox-cell input[type="checkbox"] {
  width: 18px;
  height: 18px;
  cursor: pointer;
}

.user-name {
  font-weight: 500;
  color: #212529;
}

.user-email {
  color: #6c757d;
}

.user-date {
  color: #6c757d;
  font-size: 13px;
}

.list-actions {
  display: flex;
  gap: 4px;
  justify-content: flex-end;
}

.list-actions .btn {
  padding: 4px 8px;
  font-size: 12px;
  min-width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* View Transition */
.view-transition {
  animation: viewTransition var(--animation-duration-normal) var(--animation-easing-ease-out);
}

.view-transition.transitioning {
  opacity: 0.5;
  transform: scale(0.98);
  transition: all var(--animation-duration-fast) var(--animation-easing-ease-in-out);
}

/* Staggered Animations */
.users-grid .user-card {
  animation: staggerIn var(--animation-duration-normal) var(--animation-easing-ease-out);
  animation-fill-mode: both;
}

.users-grid .user-card:nth-child(1) { animation-delay: 0.05s; }
.users-grid .user-card:nth-child(2) { animation-delay: 0.1s; }
.users-grid .user-card:nth-child(3) { animation-delay: 0.15s; }
.users-grid .user-card:nth-child(4) { animation-delay: 0.2s; }
.users-grid .user-card:nth-child(5) { animation-delay: 0.25s; }
.users-grid .user-card:nth-child(6) { animation-delay: 0.3s; }
.users-grid .user-card:nth-child(7) { animation-delay: 0.35s; }
.users-grid .user-card:nth-child(8) { animation-delay: 0.4s; }
.users-grid .user-card:nth-child(9) { animation-delay: 0.45s; }
.users-grid .user-card:nth-child(10) { animation-delay: 0.5s; }

/* Micro-interactions */
.user-card:hover {
  transform: translateY(-4px) scale(1.02);
  box-shadow: 0 8px 25px rgba(0,0,0,0.15);
}

.list-row:hover {
  background: #f8f9fa;
  transform: translateX(5px);
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

/* Focus animations */
input:focus, textarea:focus, select:focus {
  animation: glow 2s ease-in-out infinite;
}

/* Error shake animation */
.error-shake {
  animation: shake 0.5s ease-in-out;
}

/* Success bounce animation */
.success-bounce {
  animation: bounceIn 0.6s var(--animation-easing-bounce);
}

